/* normal grid node style */
.grid-node {
  display: flex;
  justify-content: center;
  align-items: center;

  flex: 1 0;
  height: auto;
  font-size: clamp(6px, 1vw, 32px);

  border: 1px solid black;

  transition: border-color 1000ms ease-out, background-color ease-out 1000ms;
  background-color: #ecf8f8;
}

@media only screen and (min-width: 992px) {
  .grid-node {
    border: 1.5px solid black;
  }
}

.grid-node:after {
  content: "";
  display: block;
  padding-bottom: 100%;
  opacity: 0;
}

/* Style the emojis */
.content {
  position: absolute;
  font-size: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* handle the walls */
.no-wall-left {
  border-left-color: rgb(96, 122, 133, 0.05);
}

.no-wall-right {
  border-right-color: rgb(96, 122, 133, 0.05);
}

.no-wall-top {
  border-top-color: rgb(96, 122, 133, 0.05);
}

.no-wall-bottom {
  border-bottom-color: rgb(96, 122, 133, 0.05);
}

.mud-left {
  border-left-color: #bc6c25;
}

.mud-right {
  border-right-color: #bc6c25;
}

.mud-top {
  border-top-color: #bc6c25;
}

.mud-bottom {
  border-bottom-color: #bc6c25;
}

.mud-left,
.mud-right,
.mud-top,
.mud-bottom {
}

/* visited nodes styles */
.visited-node {
  background-color: #0077b6;
  /* transition: background-color 1250ms ease-out; */
  animation: visitedAnim 1000ms ease-in;
}

@keyframes visitedAnim {
  0% {
    background-color: #ecf8f8;
  }

  30% {
    background-color: #0096c7;
  }

  100% {
    background-color: #0077b6;
  }
}

/* shortest path nodes */
.shortest-path-node {
  background-color: #48cae4;
  transition: background-color 500ms ease-in;
}
